      bottom: s(-290);
      right: s(65);
      @include block(s(395), s(348));
    }
  }
  &__title {
    text-transform: uppercase;
    margin-bottom: s(86);
    font-size: s(82);
    font-weight: 700;
    line-height: 110%;
    color: var(--red);
  }
  &__desc {
    width: 100%;
    max-width: s(865);
    padding-left: s(79);
    font-family: 'GraphikLCG', sans-serif;
    font-size: s(28);
    font-weight: 400;
    line-height: 160%;
    & strong {
      font-weight: 600;
    }
  }
  @include mobile {
    overflow: hidden;
    padding-top: s-md(135);
    padding-bottom: s-md(116);
    &::before {
      @include block(100%, s-md(736));
      top: s-md(-7);
      left: s-md(-35);
    }
    & > svg {
      top: 28.3rem;
      right: s-md(-337);
      @include block(s-md(1050), s-md(260));
    }
    &__container {
      flex-direction: column;
    }
    &__img {
      top: s-md(-20);
      @include block(s-md(257), s-md(299));
      &-box {
        padding-left: s-md(36);
        &::before {
