        }
      }
      &--shadow {
        top: 53rem;
        right: 2.5rem;
        bottom: initial;
        @include block(s-md(286), s-md(252));
      }
    }
    &__title {
      position: absolute;
      top: s-md(56);
      left: s-md(16);
      font-size: s-md(29);
    }
    &__desc {
      position: relative;
      padding-top: s-md(23);
      z-index: 5;
      font-size: s-md(20);
      padding-left: 0;
      @include text-bg('w');
      & strong {
        font-family: 'EuropeExt';
        font-weight: 700;
        letter-spacing: 0.04em;
      }
      & span.mobile {
        font-size: 5rem;
      }
    }
  }
}
