.header {
  position: relative;
  z-index: 5;
  padding-top: s(32);
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo {
    @include block(s(330), s(55));
    & svg {
      @include block(inherit);
    }
  }
  &__tel {
    letter-spacing: 0.065em;
  }
  @include mobile {
    padding-top: s-md(30);
    &__logo {
      @include block(s-md(238), s-md(38));
      margin-left: s-md(-8);
    }
    &__burger {
      @include flex-50-50();
      @include block(s-md(53));
      border-radius: 100%;
      border: s-md(0.5) solid var(--white);
      & svg {
        @include block(s-md(25), s-md(18));
      }
    }
  }
}

.nav {
  margin-right: s(124);
  padding: s(12) s(26) s(12) s(28);
  border: s(1) solid var(--white);
  border-radius: s(39);
  &__list {
    display: flex;
    align-items: center;
    gap: s(40);
  }
  &__link {
    text-transform: uppercase;
    letter-spacing: 0.04em;
  }
  @include mobile {
    position: absolute;
    visibility: hidden;
    overflow: hidden;
    top: s-md(100);
    left: 0;
    opacity: 0;
    z-index: 50;
    @include block(100%, auto);
    padding: s-md(39) s-md(16) s-md(49);
    border: none;
    border-radius: 0;
    background-color: var(--grey-shadow);
    @include transitions((visibility, opacity));
    &.active {
      visibility: visible;
      opacity: 1;
    }
    &__list {
      align-items: flex-start;
      flex-direction: column;
      gap: s-md(32);
      padding-bottom: s-md(43.5);
    }
    &__link {
      font-size: s-md(26);
      line-height: 130%;
      letter-spacing: 0;
    }
    &__tel {
      display: inline-block;
      text-transform: uppercase;
      margin-bottom: s-md(59);
      border: s-md(1) solid var(--white);
      border-radius: s-md(1000);
      padding: s-md(14.5) s-md(24);
      font-size: s-md(13);
      font-weight: 700;
      line-height: s-md(12);
      letter-spacing: 0.04em;
    }
    &__img {
      position: relative;
      right: s-md(-55);
      z-index: 3;
      @include img(s-md(346), s-md(248));
      &-box {
        position: relative;
        &::before,
        &::after {
          position: absolute;
          content: '';
          z-index: 1;
        }
        &::before {
          bottom: s-md(31);
          left: s-md(-206);
          @include block(s-md(265), s-md(105));
          background: linear-gradient(270deg, rgba(255, 255, 255, 0.03) 37.97%, rgba(255, 255, 255, 0) 91.6%);
        }
        &::after {
          top: s-md(121);
          right: s-md(-74);
          @include block(s-md(179), s-md(131));
          background: linear-gradient(93.47deg, rgba(155, 50, 46, 0.12) 27.09%, rgba(157, 22, 46, 0.12) 27.09%, rgba(229, 85, 79, 0) 85.93%);
        }
        & > svg {
          position: absolute;
          left: s-md(-562);
          bottom: s-md(-20);
          z-index: 2;
          @include block(s-md(1050), s-md(250));
        }
      }
    }
  }
}
