          transform: matrix(-0.87, 0.5, 0.87, 0.5, 0, 0);
          @include block(s(334), s(329));
        }
        &:nth-child(2) {
          left: s(-50);
          bottom: s(87);
          transform: matrix(-0.87, -0.5, 0, 1, 0, 0);
          @include block(s(334), s(294));
        }
        &:last-child {
