  &__date {
    text-transform: uppercase;
    font-size: s(18);
    font-weight: 700;
    line-height: 160%;
    letter-spacing: 0.04em;
  }
  &__title {
    text-transform: uppercase;
    margin-bottom: s(45);
    font-size: s(35);
    font-weight: 700;
    line-height: 110%;
    color: var(--red);
  }
  &__desc {
    display: inline-flex;
    flex-direction: column;
    text-align: justify;
    font-family: 'GraphikLCG';
    font-weight: 400;
    font-size: s(24);
    line-height: 140%;
    & > span:not(:last-child) {
      margin-bottom: s(23);
    }
  }
  &__link {
    text-transform: uppercase;
    padding: s(8) s(27) s(7);
    border: s(1) solid var(--white);
    border-radius: s(30);
    font-size: s(14);
    line-height: 160%;
    letter-spacing: 0.04em;
    background-color: rgba(255, 255, 255, 0.15);
    @include transitions((background-color, border-color));
    &:hover,
    &:active {
      border-color: var(--red);
      background-color: var(--red);
    }
  }
  @include mobile {
    padding-top: s-md(58);
    &__container {
      flex-direction: column;
    }
    &__cube {
      &--left {
        display: none;
      }
      &--right {
        top: s-md(563);
        right: s-md(-160);
      }
    }
    &__content {
      padding-right: 0;
      &--left {
        flex-direction: row;
        justify-content: space-between;
        padding-bottom: s-md(30);
      }
    }
    &__back {
      margin-bottom: 0;
      &--icon {
        @include block(s-md(53));
        margin-bottom: s-md(11);
        & > svg {
          @include block(s-md(20));
        }
      }
      &--desc {
        font-size: s-md(14);
        line-height: 120%;
      }
    }
    &__date {
      padding-top: s-md(60);
      font-size: s-md(14);
      line-height: 150%;
      letter-spacing: 0;
    }
    &__title {
      margin-bottom: s-md(17);
      font-size: s-md(22);
      line-height: 132%;
    }
    &__desc {
      text-align: initial;
      font-size: s-md(18);
      line-height: 128%;
      letter-spacing: 0.02em;
      & > span:not(:last-child) {
        margin-bottom: s-md(22);
      }
    }
    &__link {
      position: absolute;
      top: s-md(57.5);
      right: s-md(16);
      border-radius: s-md(30);
      padding: s-md(9) s-md(21);
      font-size: s-md(12);
      line-height: 160%;
