      }
    }
    &__desc {
      max-width: s-md(280);
      font-size: s-md(14);
      line-height: 130%;
    }
    &__map {
      @include block(100%, s-md(676));
      &-container {
        &::before,
        &::after {
          @include block(100%, s-md(137));
        }
      }
    }
  }
}

.contact-page,
.about-page,
.projects-page,
.article-page,
.smartmoney-page {
  & .contact {
    &__box--left {
      padding-top: s(37);
    }
    &__title {
      margin-bottom: s(28);
      font-size: s(60);
      line-height: 110%;
    }
  }
  @include mobile {
    & .contact {
      margin-bottom: 0;
    }
    & .contact {
      &__box--left {
        padding-top: s-md(26);
      }
      &__title {
        margin-bottom: s-md(10);
        letter-spacing: -0.03em;
      }
    }
  }
}
